.line {
    stroke: #000000;
    stroke-width: 3;
  }  
  
  .main {
    height:100vh;
    width:100%;
    padding:22rem 0 0 0;
  }
  .anime_box {
    margin-left:26rem;
    margin-top:-13rem;
  }
  .legacy_text {
    color:#fff;
    /* font-family: 'Gabriela', serif; */
    font-size:3rem;
    animation:display 4s;
    padding:0px !important;
    margin: 0px !important;
  }
  .shop_text {
    color:#fff;
    /* font-family: 'Gabriela', serif; */
    font-size:1.8rem;
    animation:display 4s;
  }
  .illustration {
    animation:display 4s;
    width:27rem;
  }
  
  @keyframes display {
    0%{opacity:0;}
    100%{opacity:1;}
  }
  
  /* svg anime */
  .text-line {
    stroke-dasharray: 500;
    stroke-dashoffset: 500;
    animation: dash 3s linear forwards, filling 3s ease-in forwards;
    font-size: 12rem;
  }
  
  .text-line text {
    font-family: 'Gabriela', serif;
    font-weight: normal;
    font-style: normal;
  }
  
  @keyframes dash {
    to {
        stroke-dashoffset: 0;
    }
  }
  
  @keyframes filling {
    0%, 90% { fill: transparent; fill-opacity: 0; }
    100% { fill: transparent; fill-opacity: 1; }
  }
  /* svg anime */
  
  /* media query */
  @media only screen and (max-width:1440px) {
    .anime_box {
        margin-left:22.5rem;
        margin-top:-14rem;
    }
  }
  @media only screen and (max-width:1240px) {
    .anime_box {
        margin-left:10rem;
    }
  }
  @media only screen and (max-width:768px) {

    .text-line {
      stroke-dasharray: 500;
      stroke-dashoffset: 500;
      animation: dash 3s linear forwards, filling 3s ease-in forwards;
      font-size: 7rem;
    }

    .main {
        padding:20rem 0 0 0;
    }
    .anime_box {
        width:38rem;
        margin-left:5rem;
        margin-top:-13rem;
    }
    .legacy_text {
        font-size:3rem;
    }
    .shop_text {
        font-size:1.6rem;
    }
  }
  @media only screen and (max-width:455px) {
    .anime_box {
        display:none;
    }
    .legacy_text {
        font-size:2rem;
    }
    .shop_text {
        font-size:1.4rem;
    }
    .illustration {
        width:15rem;
    }
  }
  @media only screen and (max-width:425px) {
    
  }
  @media only screen and (max-width:375px) {
    .shop_text {
        font-size:1.2rem;
    }
  }
  @media only screen and (max-width:320px) {
    .legacy_text {
        font-size:1.6rem;
    }
    .shop_text {
        font-size:1.2rem;
    }
  }
  